<template>
	<view class="uni-margin-wrap">
		<swiper :style='{height:widHeight}' class="swiper" circular indicator-dots :autoplay="autoplay"
			:interval="interval" :duration="duration" @change="imageChange">
			<swiper-item v-for="(item,index) in data" :key="index">
				<image :src="item.imgUrl" mode="widthFix" class="swiper-item" @load="imageLoad($event,index)">
				</image>
			</swiper-item>

		</swiper>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				autoplay: true,
				interval: 5000,
				duration: 500,
				widHeight: 0,
				widHeightList: []
			}
		},
		props: {
			data: {
				type: Array,
				default: () => []
			}
		},
		methods: {
			imageLoad(e, index) {
				let width = e.detail.width
				let height = e.detail.height
				let dif = Math.floor(750 / width)
				height = Math.floor((height * 750) / width)
				this.widHeightList[index] = height
				if (index === 0) {
					this.widHeight = height + 'rpx'
				}
			},
			imageChange(e) {
				let current = e.detail.current
				this.widHeight = this.widHeightList[current] + 'rpx'
			}
		}

	}
</script>

<style scoped>
	.swiper-item {
		display: block;
		width: 100%;
	}
</style>
